.register-login-module {
    .modal-heading {
        font-size: 48px;
        font-weight: normal;
        margin: 16px 0px;
        text-transform: uppercase;
    }
    // .tabs-nav {
    //     li {
    //         a {
    //             font-size: 16px;
    //             font-weight: bold;
    //             text-decoration: none;
    //             text-transform: capitalize;
    //             align-self: flex-start;
    //             margin-right: 16px;
    //             padding-bottom: 8px;
    //             color: #767676;
    //         }
    //         .active {
    //             a {
    //                 color: #333;
    //                 &:after {
    //                     height: 1.5px;
    //                     background: #333;
    //                 }
    //             }
    //         }
    //     }
    // }
    &.modal-popup {
        .modal-wrapper {
            width: 380px;
        }
    }
    .register-login-tabs-nav {
        border-bottom: 1.05536px solid rgba(0, 0, 0, 0.1);
        ul {
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0;
            li {
                display: flex;
                align-items: center;
                list-style: none;
                @media only screen and (min-width: 546px) {
                    margin-right: 55px;
                }
                &:last-child {
                    margin-right: 0;
                }
                &.active {
                    color: #000;
                    button,
                    a {
                        color: inherit;
                        position: relative;
                        &::after {
                            content: "";
                            position: absolute;
                            height: 4px;
                            background: #000000;
                            width: 100%;
                            bottom: -2px;
                            left: 0;
                        }
                    }
                }
            }
            button,
            a {
                font-weight: 700;
                font-size: 13px;
                line-height: 16px;
                text-align: center;
                text-transform: uppercase;
                padding: 12px 20px;
                border: none;
                background: none;
                cursor: pointer;
            }
        }
    }
    .register-login-tabs-content {
        // padding: 50px 0;
        // @media only screen and (min-width: 769px) {
        //     padding: 72px 0;
        // }
        // padding: 10px;

        // margin-top: -1px;
        // overflow: hidden;
        // IMG {
        //     margin-right: 10px;
        // }
       
        width: 100%;
        .register-login-tab-wrapper {
            // height: 100%;
            &:not(:first-child) {
                display: none;
            }
        }
    }
    .modal-content{
        padding: 15px 0 10px;
    }
}
